import {
  createRouter,
  createWebHashHistory
} from 'vue-router'
import { useUserStore } from '@/store/user'
import { useRouterStore } from '@/store/router'
import { ElMessage } from 'element-plus'

export const constantRoutes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('@/views/login/index'),
    meta: { tokenUse: false }
  },
  {
    path: '/register',
    name: 'register',
    component: () => import ('@/views/register/index'),
    meta: { tokenUse: false, title: '欢迎注册物联网平台账号' }
  },
  {
    path: '/resetPassword',
    name: 'resetPassword',
    component: () => import('@/views/register'),
    meta: { tokenUse: false, title: '重置密码' }
  },
  {
    path: '/:pathMatch(.*)*',
    name: '404',
    component: () => import('@/components/404'),
    meta: { isSidebar: false, tokenUse: false }
  }
]

export const asyncRouters = [
  {
    path: '/productList',
    name: 'productList',
    component: () => import('@/views/productList/index'),
    meta: { isSidebar: false, tokenUse: true }
  },
  {
    path: '/product/:productId',
    name: 'product',
    component: () => import('@/layout/index'),
    redirect: '/product/:productId/index',
    meta: { isSidebar: true, isGroup: false, icon: 'productInfo', title: '产品详情', tokenUse: true },
    children: [
      {
        path: 'index',
        name: 'productIndex',
        component: () => import('@/views/product/index'),
        meta: { isSidebar: true, isGroup: false, icon: 'productInfo', title: '产品详情', tokenUse: true, isActiveMenu: true }
      },
      {
        path: 'deviceList',
        name: 'deviceList',
        component: () => import('@/views/deviceList/index'),
        meta: { isSidebar: true, isGroup: false, icon: 'deviceList', title: '设备列表', tokenUse: true }
      },
      {
        path: 'dataStreamTemplate',
        name: 'dataStreamTemplate',
        component: () => import('@/views/dataStream/index'),
        meta: { isSidebar: true, isGroup: false, icon: 'data-stream', title: '数据流模板', tokenUse: true }
      }
    ]
  },
  {
    path: '/product/:productId/device',
    name: 'deviceLayout',
    redirect: ':deviceId',
    component: () => import('@/layout/index'),
    meta: { isSidebar: false, tokenUse: true },
    children: [
      {
        path: ':deviceId',
        name: 'device',
        redirect: 'info',
        component: () => import('@/views/device/index'),
        meta: { isSidebar: false, tokenUse: true },
        children: [
          {
            path: 'info',
            name: 'deviceInfo',
            component: () => import('@/views/device/component/deviceInfo'),
            meta: { redirectActiveMenu: 'deviceList', title: '设备详情', subMenu: true, tokenUse: true }
          },
          {
            path: 'dataStream',
            name: 'dataStream',
            component: () => import('@/views/device/component/dataStream'),
            meta: { redirectActiveMenu: 'deviceList', title: '数据流展示', subMenu: true, tokenUse: true }
          },
          {
            path: 'command',
            name: 'command',
            component: () => import('@/views/device/component/command'),
            meta: { redirectActiveMenu: 'deviceList', title: '下发命令', subMenu: true, tokenUse: true }
          }
        ]
      }
    ]
  },
  {
    path: '/:pathMatch(.*)*',
    name: '404',
    component: () => import('@/components/404'),
    meta: { isSidebar: false, tokenUse: false }
  }
]

const createRouterOption = {
  history: createWebHashHistory(),
  routes: constantRoutes,
  scrollBehavior: () => ({
    top: 0,
    behavior: 'smooth'
  })
}

const router = createRouter(createRouterOption)

export function resetRouter() {
  asyncRouters.forEach(item => {
    if (item.name !== '404') {
      router.removeRoute(item.name)
    }
  })
}

export function asyncAddRouter() {
  const routerStore = useRouterStore()
  routerStore.isLoadAsyncRouter = true
  router.removeRoute('404')
  asyncRouters.forEach(item => {
    router.addRoute(item)
  })
}

router.beforeEach((to, from, next) => {
  // const rootPath = to.fullPath.split('/')[1]
  const userStore = useUserStore()
  const routerStore = useRouterStore()

  if (from.params.productId) {
    routerStore.productId = from.params.productId
  }

  if (routerStore.isLoadAsyncRouter) {
    //  已经加载了动态路由

    routerStore.routerName = to.meta.redirectActiveMenu ? to.meta.redirectActiveMenu : to.name
    if (to.meta.subMenu) {
      routerStore.subRouterName = to.name
    }
    //  访问需要权限的页面缺没有令牌
    if (to.meta.tokenUse && userStore.userToken === '') {
      ElMessage({
        type: 'error',
        message: '登录过期'
      })
      next({ name: 'login' })
    } else {
    //  其他情况，如访问需要权限的页面且有令牌，或访问不需要权限的页面
      next()
    }
  } else {
  //  没有加载动态路由
  /*
      首先没有加载动态路由只有两种情况，一是没有登录，直接通过地址栏输入，访问其他页面，另一种是登录后刷新，且刷新位置一定是处于需要权限的页面，还有初次进入应用这种情况
      这种情况下又分几种情况
      正常跳转下，在不需要token的页面之间跳转，直接放行即可
   */
    if (userStore.userToken !== '') {
      //  如果token不为空，说明该用户一定登录过了，所以可以直接加载动态路由
      asyncAddRouter()
      next({ path: to.fullPath })
    } else {
      next()
    }
  }
})

router.afterEach((to) => {
  const routerStore = useRouterStore()
  if (to.params.productId) {
    routerStore.productId = to.params.productId
  }
  if (to.params.deviceId) {
    routerStore.deviceId = to.params.deviceId
  }
})

export default router
